export default function BuyingPage() {
  const [selectedTariff, setSelectedTariff] = useState<string | null>(null)
  const [billingPeriod, setBillingPeriod] = useState<BillingPeriod>("month")
  const [serverName, setServerName] = useState("")
  const [loading, setLoading] = useState(false)
  const [success, setSuccess] = useState(false)
  const { t } = useLanguage()
  const { formatPrice, convertPrice } = useCurrency()

  const handleBuy = async () => {
    if (!selectedTariff || !serverName) return

    setLoading(true)
    setSuccess(false)
    const tariff = tariffs.find(t => t.id === selectedTariff)
    try {
      // Здесь должен быть реальный процесс заказа сервера через ваш API
      // Пример запроса:
      await fetch("/api/orders", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          serverName,
          serverPlan: tariff?.name,
          billingPeriod,
          price: tariff?.prices[billingPeriod],
        }),
      })

      setSuccess(true)
      setTimeout(() => setSuccess(false), 4000)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="max-w-5xl mx-auto py-8">
      <Button variant="outline" className="mb-4" type="button" onClick={() => window.history.back()}>
        ← {t("common.back") || "Вернуться назад"}
      </Button>
      <h1 className="text-3xl font-bold text-foreground mb-6">{t("servers.buyTitle") || "Купить сервер"}</h1>
      <div className="grid md:grid-cols-2 gap-8">
        {/* Tariff List */}
        <div>
          <h2 className="text-xl font-semibold mb-3">{t("servers.choosePlan") || "Выберите тариф"}</h2>
          <div className="space-y-3 max-h-[650px] overflow-y-auto pr-2">
            {tariffs.map((tariff) => (
              <div
                key={tariff.id}
                className={`transition-colors cursor-pointer border rounded-lg p-4 bg-background/70 flex items-center gap-4 ${
                  selectedTariff === tariff.id ? "border-primary shadow-lg" : "border-border/40 hover:border-primary/60"
                }`}
                onClick={() => setSelectedTariff(tariff.id)}
              >
                <div className="w-10 h-10 rounded-md flex items-center justify-center bg-primary/10">
                  <Server className="w-5 h-5 text-primary" />
                </div>
                <div className="flex-1">
                  <div className="font-semibold text-foreground">{tariff.name}</div>
                  <div className="text-sm text-muted-foreground">
                    {tariff.cores} CPU &middot; {tariff.ram} GB {tariff.ramType} &middot; {tariff.storage} GB {tariff.ramType === "DDR5" ? "" : "NVMe"}
                  </div>
                </div>
                <div className="flex flex-col gap-1 min-w-[120px]">
                  <div className="text-lg font-bold text-primary">
                    ${tariff.prices.month} <span className="text-xs text-muted-foreground">/мес</span>
                  </div>
                  <div className="text-base font-semibold text-foreground">
                    ${tariff.prices.hour} <span className="text-xs text-muted-foreground">/час</span>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
        {/* Order Panel */}
        <div className="space-y-6 max-w-md mx-auto w-full">
          <h2 className="text-xl font-semibold">{t("servers.orderConfig") || "Настройки заказа"}</h2>
          <div className="flex flex-col gap-4">
            <div>
              <label className="block mb-1 font-medium">{t("servers.nameLabel") || "Имя сервера"}</label>
              <Input
                placeholder={t("servers.namePlaceholder") || "Например, my-server"}
                value={serverName}
                onChange={e => setServerName(e.target.value)}
                disabled={loading}
              />
            </div>
            <div>
              <label className="block mb-1 font-medium">{t("servers.billing") || "Платежный период"}</label>
              <div className="flex gap-3">
                <Button
                  variant={billingPeriod === "month" ? "default" : "outline"}
                  onClick={() => setBillingPeriod("month")}
                  size="sm"
                  type="button"
                  disabled={loading}
                >
                  {t("servers.monthly") || "Месяц"}
                </Button>
                <Button
                  variant={billingPeriod === "hour" ? "default" : "outline"}
                  onClick={() => setBillingPeriod("hour")}
                  size="sm"
                  type="button"
                  disabled={loading}
                >
                  {t("servers.hourly") || "Час"}
                </Button>
              </div>
            </div>
            {selectedTariff && (
              <div>
                <p className="font-semibold mb-1">{t("servers.selectedPlan") || "Выбранный тариф:"}</p>
                <div className="flex items-center gap-2">
                  <Badge>{tariffs.find(t => t.id === selectedTariff)?.name}</Badge>
                  <span className="ml-4">
                    {billingPeriod === "month"
                      ? `$${tariffs.find(t => t.id === selectedTariff)?.prices.month}/мес`
                      : `$${tariffs.find(t => t.id === selectedTariff)?.prices.hour}/час`}
                  </span>
                </div>
              </div>
            )}
            <Button
              className="w-full mt-3"
              onClick={handleBuy}
              disabled={!selectedTariff || !serverName || loading}
            >
              {loading
                ? t("servers.buying") || "Покупка..."
                : t("servers.buyButton") || "Купить"}
            </Button>
            {success && (
              <div className="text-green-600 font-medium mt-2">
                {t("servers.successOrder") || "Сервер успешно заказан!"}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}
